@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Poppins:wght@300&display=swap&family=Montserrat:wght@500&display=swap&family=Shippori+Antique&display=swap');

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

body {
  font-family: 'Poppins', sans-serif;
}

.container {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
}

/* NAVBAR */

nav {
  position: fixed;
  z-index: 1;
  top: 0;
  width: 100%;
  font-family: 'Poppins', sans-serif;
  color: rgb(223, 223, 223);
  border-bottom: 1px solid #ffffff37;
}

.interface-obj-dark {
  background: rgb(12, 0, 52);
}

.interface-obj-light {
  background: #00067e;
}

nav a {
  color: rgb(223, 223, 223);
}

.nav-list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
  gap: 2rem;
}

.nav-logo {
  font-size: 2.5rem;
  font-family: 'Montserrat', sans-serif;
  margin-right: auto;
  color: rgb(223, 223, 223);
}

.nav-logo a {
  color: rgb(223, 223, 223);
}

.nav-item {
  flex-shrink: 0;
  flex-grow: 0;
  font-weight: bold;
  padding: 1.5rem 1.8rem;
  position: relative;
  cursor: pointer;
  color: rgb(223, 223, 223);
}

.nav-item:after {
  position: absolute;
  content: '';
  width: 0%;
  height: 5px;
  background: #23cbfa;
  border-radius: 4px;
  bottom: 1em;
  left: 1.6em;
  transition: 0.3s;
}
.nav-item:hover::after {
  width: 60%;
}

.nav-btn button {
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  background: #23cbfa;
  color: #063ab3;
  font-family: inherit;
  font-size: inherit;
  font-weight: bold;
  cursor: pointer;
  transition: 0.1s;
}

.nav-btn button:hover {
  background: rgba(35, 201, 247, 0.564);
}

/* TOOLBAR */
.light-theme {
  background: #fafafa;
  color: #000 !important;
}

.dark-theme {
  background: rgb(15, 14, 22);
  color: #ffffff !important;
}

.theme-switch {
  display: flex;
  align-items: center;
}

.theme-switch__icon {
  color: currentColor;
  height: 24px;
  width: 24px;
  margin-right: 8px;
  margin-left: 8px;
}

.theme-switch__control {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: relative;
  width: 64px;
  height: 32px;
  border-radius: 50em;
  padding: 3px 0;
}

.theme-switch__toggle {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
}

.theme-switch__track {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: transparent;
  user-select: none;
  background-color: rgb(22, 21, 27);
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
}

.theme-switch__marker {
  position: relative;
  background-color: #fff;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  z-index: 2;
  pointer-events: none;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
  transform: translateX(3px);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #d3d3d4;
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(35px);
}

