/* SEARCH SECTION */

.hello-section {
  margin-top: 66.4px;
  min-height: 350px;
  background-image: url('https://www.themoviedb.org/t/p/w1920_and_h600_multi_faces/jTiQAusGG4dVLSb1WHme9I9R4NN.jpg');
  background-color: rgba(0, 0, 0, 0.8);
  background-blend-mode: darken;
  background-position: center;
  background-size: cover;
}

.search-section-cont {
  color: white;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 50px;
}

.search-title h2 {
  font-size: 60px;
  margin-bottom: 50px;
  color: rgb(223, 223, 223);
}

.search-title p {
  color: rgb(223, 223, 223);
  margin-bottom: 40px;
  font-size: 30px;
}

.search label input {
  padding-left: 10px;
  width: 80%;
  font-weight: 600;
  color: rgb(109, 109, 109);
  height: 40px;
  border-radius: 50px;
  border: none;
  margin: 0;
  outline: none;
}

.search input[type='submit'] {
  font-weight: 600;
  color: rgb(109, 109, 109);
  height: 40px;
  border-radius: 50px;
}

.search-results {
  z-index: 1;
  position: absolute;
  background: #fafafa;
  list-style: none;
  width: 80%;
  border-left: solid 2px grey;
  border-right: solid 2px grey;
  color: black;
}

.search-results li {
  border-bottom: solid 2px grey;
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
}

.search-results a {
  color: rgb(109, 109, 109);
  font-weight: 600;
}

/* NEW RELEASES */
.popular-title {
  font-weight: 500;
  color: rgb(88, 88, 88);
}
.popular-section {
  min-height: 100vh;
  margin-bottom: 30px;
}

.popular-cont {
  padding-top: 80px;
}

.film-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 35px;
}

.popular-film {
  display: flex;
  flex-direction: column;
  width: 150px;
  position: relative;
  height: 220px;
  margin: 20px 20px 60px;
  font-family: 'Shippori Antique';
}

.popular-film img {
  border-radius: 20px;
  width: 100%;
  height: 100%;
  position: relative;
  margin-bottom: 10px;
}

.popular-film-rating {
  position: absolute;
  color: white;
  bottom: 0;
  right: 0;
  width: 50px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  border-bottom-right-radius: 18px;
  border-top-left-radius: 18px;
  background: rgb(255, 174, 0);
}

.popular-film-title {
  text-align: center;
  color: rgb(121, 121, 121);
  font-weight: 400;
}

footer {
  text-align: center;
  color: #fff;
  padding: 35px 0;
}

.copyright {
  display: flex;
  text-align: center;
  justify-content: space-around;
  margin-bottom: 30px;
  color: rgb(212, 212, 212);
}

.mail svg {
  width: 35px;
  height: 35px;
  transition: all 0.3s;
  color: rgb(230, 230, 230);
  margin-right: 50px;
}

.mail svg:hover {
  color: #ffbb00;
}

.telegram svg {
  width: 35px;
  height: 35px;
  color: rgb(230, 230, 230);
  transition: all 0.3s;
  margin-right: 50px;
}

.telegram svg:hover {
  color: #0bb1f3;
}

.telephone svg {
  width: 35px;
  height: 35px;
  color: rgb(230, 230, 230);
  transition: all 0.3s;
}

.telephone svg:hover {
  color: #0ba705;
}
